*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: size-desktop($fz-default);
  line-height: size(24px);
  font-family: $ff-graphik;
  color: $color-default-black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏—è –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏—è —à—Ä–∏—Ñ—Ç–∞ –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ —Ü–µ–Ω—Ç—Ä–∞ —Å—Ç—Ä–æ–∫–∏
  text-rendering: optimizeLegibility;
  // –µ—Å–ª–∏ –ø–æ –ø—Ä–µ–∂–Ω–µ–º—É –µ—Å—Ç—å –ø—Ä–æ–±–ª–µ–º—ã —Å –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ–º
  // https://transfonter.org/ - –≤–∫–ª—é—á–∏—Ç–µ –Ω–∞—Å—Ç—Ä–æ–π–∫—É https://prnt.sc/12rnt6g –∏ –ø–µ—Ä–µ–∫–æ–Ω–≤–µ—Ä—Ç–∏—Ä—É–π—Ç–µ —à—Ä–∏—Ñ—Ç

  @include vp-1023 {
    font-size: $fz-default;
    line-height: 24px;
  }
}

body {
  width: 100%;
  height: 100%;

  background-color: $color-default-white;
}

a {
  color: $color-default-black;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
// –µ—Å–ª–∏ –Ω–∞ –ø—Ä–æ–µ–∫—Ç–µ —É –∏–Ω–ø—É—Ç–æ–≤ –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è —Ä–∞–∑–Ω—ã–µ —Ü–≤–µ—Ç–∞ —Ñ–æ–Ω–æ–≤\—Ç–µ–∫—Å—Ç–∞ -
// —É–¥–∞–ª–∏—Ç–µ —ç—Ç–æ –∏–∑ –≥–ª–æ–±–∞–ª–∞ –∏ –∏—Å–ø–æ–ª—å–∑—É–π—Ç–µ –ª–æ–∫–∞–ª—å–Ω–æ —Å –Ω—É–∂–Ω—ã–º–∏ —Ü–≤–µ—Ç–∞–º–∏
// rgba –Ω–µ –ø–æ–¥–æ–π–¥–µ—Ç, —Å–∫–æ–Ω–≤–µ—Ä—Ç–∏—Ä—É–π—Ç–µ —Ü–≤–µ—Ç –≤ hex –±–µ–∑ –ø—Ä–æ–∑—Ä–∞—á–Ω–æ—Å—Ç–∏
// –µ—Å–ª–∏ –≤ —Å—Ç–∏–ª—è—Ö —É–∂–µ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è box-shadow –µ—Å—Ç—å –¥—Ä—É–≥–æ–µ —Ä–µ—à–µ–Ω–∏–µ -
// –∑–∞–¥–∞—Ç—å –∫ —Å–ø–∏—Å–∫—É —Ç—Ä–∞–Ω–∑–∏—à–µ–Ω–æ–≤ `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
